<template>
    <div class="login-page-div">
        <div class="landing-header">
            <a href="#"></a>
        </div>
        <div class="landing-body-warpper">
            <div class="promotion-banner">
                <img src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg" alt="login">
            </div>
            <div class="login-section">
                  <form action="#">
                      <div :class="{'validate-info-active':!isCanLogin,'validate-info':isCanLogin}">
                          <i class="el-icon-remove"></i>
                          <span>
                              {{errorMessage}}
                          </span>
                      </div>
                      <p class="login-type" v-if="phoneToLogin">
                          <span>账号登陆</span>
                          <a href="#" @click="changeLoginMethod">手机动态码登陆
                              <i class="el-icon-mobile-phone"></i>
                          </a>
                      </p>
                      <p class="login-type" v-else>
                          <span>账号登陆</span>
                          <a href="#" @click="changeLoginMethod">普通方式登陆
                              <i class="el-icon-star-on"></i>
                          </a>
                      </p>
                      <div :class="{'phone-input-wrapper':true,'NocanLogin':isCanLogin}" >
                         
                               <span>+86 ></span>
                          <input type="text" name="email" id="" placeholder="手机号" v-model="accountNumber"
                                 @click="write">
                          
                      </div>
                  
                      <div class="pw-input-wrapper" v-if="phoneToLogin">
                          <i class="el-icon-news"></i>
                          <input type="text" name="pw" id="" placeholder="密码" v-model="passWord" 
                                 :class="{'NocanLogin':isCanLogin}" @input="write">
                      </div>
                      <div class="pw-input-wrapper" v-else>
                          <i class="el-icon-news"></i>
                          <input type="text" name="pw" id="" placeholder="动态码">
                          <input type="text" class="get-phone-code" placeholder="获取手机动态码" disabled>
                      </div>
                      <p class="forget-pw">
                          忘记密码?
                      </p>
                      <div class="login-div">
                          <input type="text" name="login" id="" value="登陆" @click="login">
                      </div>
                      <p class="signup-guide">
                          <span>还没有账号?</span>
                          <router-link tag="a" :to="{name:'register'}">免费注册</router-link>
                      </p>
                  </form>
            </div>
        </div>
        <div class="landing-footer">
            <div class="landing-footer-nav">
                <ul>
                    <li>关于美团</li>
                    <li>加入我们</li>
                    <li>商家入驻</li>
                    <li>帮助中心</li>
                    <li>美团手机版</li>
                </ul>
            </div>
            <p class="copyright">
                © 2019
                <a href="#">美团网团购</a>
                 meituan.com  
                <a href="#">京ICP证070791号 </a>
                京公网安备11010502025545号
            </p>
        </div>
    </div>
</template>

<script>
export default {
  methods:{
      changeLoginMethod(){
          this.phoneToLogin = !this.phoneToLogin;
      },
      login(){
         
          if(this.accountNumber == '' || this.passWord == ''){
              console.log('213');
              this.isCanLogin = true;
              this.errorMessage = '请输入账号和密码'
              return false;
          }
      },
      write(){
          this.isCanLogin = false;
          this.errorMessage = '';
      }
  },
  data(){
      return {
          phoneToLogin:true,
          accountNumber:"",
          passWord:"",
          isCanLogin:false,
          errorMessage:''
      }
  }
}
</script>

<style lang="less">
  @import url('../../assets/landing/landing.less');
</style>



